import { FileTreePanelPackageView } from './views';
import { Package, GSP } from '@farris/ide-devkit';
// import {Workspace} from '../../workspace/workspace';
import { ComponentFactoryResolver, Injector, Type } from '@angular/core';
import { FileTreePanelState } from './views/file-tree-panel/file-tree-panel.state';
import { FileTreePanelService, FILE_TREE_PANEL_EVENTS } from './views/file-tree-panel/file-tree-panel.service';
import { FileTreePanelComponent } from './views/file-tree-panel/file-tree-panel.component';

const id = 'filetree';

export class FileTreePanelPackage extends Package {
  layoutPanel: any;
  modelPanel: any;
  subscriptions: null;
  fileTreePanelState: FileTreePanelState;
  fileTreePanelService: FileTreePanelService;

  constructor(gsp: GSP, private cfr: ComponentFactoryResolver, private injector: Injector) {
    super(gsp);

  }

  activate(state) {
    this.packageView = new FileTreePanelPackageView(state.fileTreePanelPackageViewState, this.cfr, this.injector);
    console.log('file tree panel package is activated.');
    this.layoutPanel = this.gsp.workspace.getLeftDock().getActivePane().addItem({
      id: id,
      getTitle: () => '文件',
      getElement: () => this.packageView.getElement()
    }, {});

    // 处理file-tree-panel上的事件
    this.fileTreePanelState = this.injector.get(FileTreePanelState);
    this.fileTreePanelState.fileTreeNodeDoubleClick.subscribe((data) => this.fileNodeDblClicked(data));
    this.fileTreePanelState.fileTreeNodeFocused.subscribe((data) => this.fileNodeFocused(data));
    this.fileTreePanelState.currentGitReposity.subscribe((data) => this.sendCurrentGitReposity(data));
    this.fileTreePanelState.currentGitBranch.subscribe((data) => this.sendCurrentGitBranch(data));


    this.fileTreePanelService = this.injector.get(FileTreePanelService);

    // 处理file-tree-panel 对外提供的接口
    this.gsp.eventBus.responseOn('FileTreePanelPackage', 'AccessFileTree', (requestData) => this.accessFileTree(requestData));


    // 调用 file-tree-panel 对外提供的接口
    // requestFor(target: string, tokenValue: string, requestName: string, requestValue: any, success: (any) => any, fail?: (string) => any): void;
    // this.gsp.eventBus.requestFor('FileTreePanelPackage', null, 'AccessFileTree', {action: '5'},(responseData) => this.successAccessedfileTree(responseData));
    // this.gsp.eventBus.requestFor('FileTreePanelPackage', null, 'AccessFileTree', {action: '6'},(responseData) => this.successAccessedfileTree(responseData));
    const nodeInfo = {
      action: '1',               // 新增节点（仅支持选中已有节点后新增节点）
      node: {
        nodeID: '1',             // 节点id,非空（暂时可使用静态值）
        nodeName: 'foo.ts',      // 节点名称
        nodeType: 'file',        // 节点类型
        nodePath: '/'            // 节点路径
        // customData: 'foo1'    // 自定义数据，支持增加并使用自定义数据
      }
    };
    this.gsp.eventBus.requestFor('FileTreePanelPackage', null, 'AccessFileTree', nodeInfo, (responseData) => this.successAccessedfileTree(responseData));

  }
  private successAccessedfileTree(responseData) {
    console.log('Response Data from the file tree component is:' + responseData);
  }
  public accessFileTree(request: any): any {
    if (request == null) {
      return;
    }
    console.log('request data is:' + request);

    // this.fileTreePanelState.fileTreeAccess.emit(request);
    // this.fileTreePanelService.fireEvent({ eventName: FILE_TREE_PANEL_EVENTS.onFileTreeAccess, requestData: request });
    const fileTreePanel = this.packageView.getElement().instance as FileTreePanelComponent;
    return fileTreePanel.processRequest(request);
  }

  deactivate() {
    this.packageView.destroy();
    console.log('file tree panel package is deactivated');
  }

  serialize() {
    return {
      fileTreePackageViewState: this.packageView.serialize()
    };
  }

  private fileNodeDblClicked(data) {
    // this.gsp.eventBus.post(FileTreePanelPackage, this.constructor.name, 'FileNodeDblClicked', data);

    this.gsp.workspace.open(data.path);
  }

  private fileNodeFocused(data) {
    this.gsp.eventBus.post(FileTreePanelPackage, this.constructor.name, 'FocusedNodePathInfo', data);
  }

  private sendCurrentGitReposity(data) {
    this.gsp.eventBus.post('FileTreePanelPackage', this.constructor.name, 'CurrentGitReposity', data);
  }

  private sendCurrentGitBranch(data) {
    this.gsp.eventBus.post('FileTreePanelPackage', this.constructor.name, 'CurrentGitBranch', data);
  }
}



