import { Injectable, EventEmitter } from '@angular/core';

@Injectable()
export class FileTreePanelState {
  fileTreeNodeActived = new EventEmitter<any>();
  fileTreeNodeDoubleClick = new EventEmitter<any>();
  fileTreeNodeFocused = new EventEmitter<any>();
  fileTreeAccess = new EventEmitter<any>();
  currentGitReposity = new EventEmitter<any>();
  currentGitBranch = new EventEmitter<any>();
}



