import { extend } from 'lodash';
export class FileTreePanelOptions {
  actionField = 'action';
  displayField = 'name';
  fileTypeField = 'type';
  pathField = 'path';
  constructor(options = {}) {
    extend(this, options);
  }
}

// todo: 迁移到独立的常量文件中
export const ACTION = {
  // 增加树节点
  createFileTreeNode: '1',
  // 查询树节点
  retrieveFileTreeNode: '2',
  // 更新树节点
  updateFileTreeNode: '3',
  // 删除树节点
  deleteFileTreeNode: '4',
  // 刷新树
  refreshFileTree: '5',
  // 获取当前选中节点
  getFocusedFileTreeNode: '6',
};





