import { Injectable } from '@angular/core';
import { HttpClient, HttpErrorResponse, HttpResponseBase } from '@angular/common/http';
import { Subject } from 'rxjs/Subject';
import { Observable} from 'rxjs/observable';
import { of } from 'rxjs/observable/of';
import { catchError } from 'rxjs/operators';
import { environment } from '../../../../environments/environment'; 

// access environment.SERVER_IP
const SERVER_IP: string = environment.SERVER_IP;
const GIT_BASE_PATH: string = '/api/dev/main/v1.0/git';
const GIT_ADDR: string = '/addr';
const GIT_BRANCH: string ='/branch';
const GIT_CHECKOUI: string ='/checkout';

@Injectable()
export class GitDataService {
  
  // 注入Http服务
  constructor(private http: HttpClient) { }


  /**
   * 获取指定路径的git仓库路径，逐级向上查找，没有则返回空
   */
  public getGitAddress(currentPath:string = ''):Subject<string>{
    let http = this.http;
    let url = this.getGitAddrUrl(currentPath);

    let gitAddrSubject = new Subject<string>();
    http.get(url, {responseType:'text'} ).subscribe(
      result => {
        // 获取git仓库的路径信息
        console.log("gitAddr:" + result);
        gitAddrSubject.next(result.toString());
      }
    );

    return gitAddrSubject;
  } 



  
  /**
   * checkout命令
   * git checkout branchName
   * @param projectPath 
   * @param branchName 
   * @param createNewBranch 
   * @param chosenfiles 
   * @param forceRecoverFile 
   */
  public postGitCheckout(projectPath: string, branchName: string, createNewBranch: boolean = false, chosenfiles: string[] = [], forceRecoverFile: boolean = false): Observable<any>{
    let url = this.getGitCheckoutUrl(projectPath);

    let body = {
      name: branchName,
      createnew: createNewBranch,
      files: chosenfiles,
      force: forceRecoverFile
    }

    return this._postGitCommand(url, body);
  }

  /**
   * 获取Git branch数据
   * @param projectPath 工程所在路径
   */
  public getGitBranchData(projectPath: string = ''): Subject<BranchData[]>{

    let http = this.http;
    let url = this.getGitBranchUrl(projectPath);

    let gitBranchDataSubject = new Subject<BranchData[]>();
    http.get(url).subscribe(
      result => {
        console.log("gitBranchArray" + result);
        let branchData: BranchData[] = this.getAdaptedGitBranchData(result);
        if(branchData.length  > 0){
          gitBranchDataSubject.next(branchData);
        }
      }
    );
    return gitBranchDataSubject;
  }

  public getAdaptedGitBranchData(result:any): BranchData[]{
    let branchArray: BranchData[] = [];

    if(result && result.length > 0){
      result.forEach(element => {
        let branch: BranchData = new BranchData(element);
        branchArray.push(branch);
      });
    }

    return branchArray;
  }

  private _postGitCommand(postUrl: string, body: any) : Observable<any>{

    let http = this.http;
    return http.post<any>(postUrl, body)
      .pipe(
        catchError(this.handleError('postGitCommand', postUrl))
      );
  }


  messages: string[] = [];
  
  add(message: string) {
    this.messages.push(message);
  }

  clear() {
    this.messages = [];
  }

  /**
   * Returns a function that handles Http operation failures.
   * This error handler lets the app continue to run as if no error occurred.
   * @param serviceName = name of the data service that attempted the operation
   * @param operation - name of the operation that failed
   * @param result - optional value to return as the observable result
   */
  handleError<T> (serviceName = '', operation = 'operation', result = {} as T) {

    return (error: HttpErrorResponse): Observable<T> => {
      // TODO: send the error to remote logging infrastructure
      console.error(error); // log to console instead

      const message = (error.error instanceof ErrorEvent) ?
        error.error.message :
       `server returned code ${error.status} with body "${error.error}"`;

      // TODO: better job of transforming error for user consumption
      this.add(`${serviceName}: ${operation} failed: ${message}`);

      // Let the app keep running by returning a safe result.
      return of( result );
    };

  }

  /**
   * get git checkout web api
   * @param projectPath
   */
  private getGitCheckoutUrl(projectPath: string): string{
    return this._getGitBaseUrl(projectPath, GIT_CHECKOUI);
  }
  
  /**
   * get git branch web api
   * @param projectPath
   * @param listMode 
   */
  private getGitBranchUrl(projectPath: string, listMode: string = '0'): string{
    let url:string = this._getGitBaseUrl(projectPath, GIT_BRANCH);
    return url += '&listMode=' + listMode;
  }

  /**
   * get git addr web api
   * @param currentRelativePath
   */
  private getGitAddrUrl(currentRelativePath: string): string{
    let localRelativePath = currentRelativePath.replace(/\\/g, '/');
    let url: string = SERVER_IP + GIT_BASE_PATH + GIT_ADDR;

    if(localRelativePath.length == 0){
      return;
    }

    url += '?wsPath=' + localRelativePath;
    
    return url;
  }

  private _getGitBaseUrl(projectPath: string, operator: string): string{
    let url: string = SERVER_IP + GIT_BASE_PATH + operator;

    if(projectPath.length == 0){
      return;
    }

    url += '?projectPath=' + projectPath;
    
    return url;
  }

}

export class BranchData {

  _fullName: string;
  _displayName: string;
  _activeBranch: boolean;
  _remoteBranch: boolean;

  constructor({name, displayName, active, remote}){
      this._fullName = name;
      this._displayName = displayName;
      this._activeBranch = active;
      this._remoteBranch = remote;
  }

  get fullName(){
      return this._fullName;
  }

  get displayName(){
      return this._displayName;
  }

  get active(){
      return this._activeBranch;
  }

  get remote(){
      return this._remoteBranch;
  }
}


