import { Injectable } from '@angular/core';
import { assign } from 'lodash';
import { TreeModel } from '../../models/tree-model';

// todo:file-tree组件从file-tree-panel组件拆分后，将其迁移到独立的文件
export const FILE_TREE_PANEL_EVENTS = {
    onFileTreeAccess: 'onFileTreeAccess'
};

@Injectable()
export class FileTreePanelService {
    eventNames = Object.keys(FILE_TREE_PANEL_EVENTS);
    events: any = {}; // 使用事件

    /**
     * 构造函数，注入依赖的服务
     * @param treeModel 注入文件树服务，以与文件树交互
     */
    constructor(public treeModel: TreeModel) {

    }

    /**
     * 设置启用事件
     * @param  事件
     */
    addEvent(event) {
        assign(this.events, event);
        console.log(this.events);
    }

    // fireEvent(event) {
    //     this.events[event.eventName].emit(event);
    // }
}



