import { Component, Input, ComponentFactoryResolver, Injector } from '@angular/core';
import { BsModalService, MessagerService } from '@farris/ui/container/modal';
import { ModalContainerComponent } from './test.modal-container.component';
import { GSP } from '@farris/ide-devkit';


@Component({
  template: `
    <button (click)="show()">click</button>
    <farris-dialog #dlg></farris-dialog>
  `,
  selector: 'app-test'
})
export class TestComponent {
  @Input() gsp: GSP;
  constructor(public dialogService: BsModalService,
     private cfr: ComponentFactoryResolver,
     private injector: Injector) {

  }

  show() {
    // const test = this.dialogService.show(ModalContainerComponent, {
    //   title: 'hello', width: 300, height: 400,
    //   initialState: {content: 'abcdefg'}
    // });

    // this.messager.info('abc');

    const compRef = this.cfr.resolveComponentFactory(ModalContainerComponent).create(this.injector);
    const item = {
      getElement: () => compRef
    };
    this.gsp.workspace.addModalPanel({item});
  }
}



