import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { TreeComponent } from './views/tree/tree.component';
import { TreenodeComponent } from './views/treenode/treenode.component';
import { TreeNodeContentComponent } from './views/tree-node-content/tree-node-content.component';
import { TreeNodeContentDirective } from './directives/tree-node-content.directive';
import { FileTreePanelComponent } from './views/file-tree-panel/file-tree-panel.component';
import { TreeModel } from './models/tree-model';
import { FileTreeDataService } from './services/file-tree-data.service';
import { GitDataService } from './services/git-data.service';
import { FileTreePanelState } from './views/file-tree-panel/file-tree-panel.state';
import { FileTreePanelService } from './views/file-tree-panel/file-tree-panel.service';
import { BsDropdownModule } from '@farris/ide-devkit';


@NgModule({
  declarations: [
    TreeComponent,
    TreenodeComponent,
    TreeNodeContentComponent,
    TreeNodeContentDirective,
    FileTreePanelComponent
  ],
  imports: [
    BsDropdownModule.forRoot(),
    CommonModule
  ],
  exports: [],
  providers: [
    FileTreePanelService,
    GitDataService, 
    TreeModel, 
    FileTreeDataService, 
    FileTreePanelState
  ],
  entryComponents: [
    FileTreePanelComponent, 
    TreeNodeContentComponent
  ]
})
export class FileTreePanelPackageModule { }



