import { PackageView } from '@farris/ide-devkit';
import {ComponentFactoryResolver, Injector} from '@angular/core';
import { FileTreePanelComponent } from './file-tree-panel/file-tree-panel.component';

export class FileTreePanelPackageView extends PackageView {
  constructor(serializedState: any, private cfr: ComponentFactoryResolver, private injector: Injector) {
    super(serializedState);
    this.element = this.cfr.resolveComponentFactory(FileTreePanelComponent).create(this.injector);
  }

  serialize() { }

  destroy() {
  }
}



