import { environment } from './../environments/environment';
import { NgModule } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { HttpClient, HttpClientModule } from '@angular/common/http';
import { LayoutModule } from '@progress/kendo-angular-layout';
import { PackageManager } from './framework/services/package-manager';
import { ModalModule } from '@farris/ide-devkit';
import { IdecontentComponent } from './components/idecontent/idecontent.component';
import { IdepropertypanelComponent } from './components/idepropertypanel/idepropertypanel.component';
import { IdecontrolpanelComponent } from './components/idecontrolpanel/idecontrolpanel.component';
import { IdecontroltoolboxComponent } from './components/idecontroltoolbox/idecontroltoolbox.component';
import { IdecontroloutlineComponent } from './components/idecontroloutline/idecontroloutline.component';
import { IdecontentwrapperComponent } from './components/idecontentwrapper/idecontentwrapper.component';
import { IdeCodeEditorComponent } from './components/ide-code-editor/ide-code-editor.component';
import { WorkspaceComponent } from './workspace/workspace.component';
import { MainPanelModule } from './ide-dev-kit/main-panel/main-panel.module';
import { ConfigService } from './services/config.service';
import { GridModule } from '@progress/kendo-angular-grid';
import { HttpModule } from '@angular/http';
import { WorkspaceManager } from './workspace/workspace.manager';
import { IDECommonModule } from './components/ide-common.module';
import { LayoutModule as IDELayoutModule } from './framework/layout/layout.module';
import { FileTreePanelPackageModule } from './packages/file-tree-panel-package/package.module';
import { FarrisDialogModule } from '@farris/ui/container/modal';
import { TestComponent } from './workspace/test.component';
import { ModalContainerComponent } from './workspace/test.modal-container.component';
import { IDERootingModule } from './ide.routes';
import { GSPMetadataServiceModule } from '@gsp-lcm/metadata-selector';
// const monacoConfig: NgxMonacoEditorConfig = {
//   baseUrl: 'assets',
//   defaultOptions: { scrollBeyondLastLine: false },
//   onMonacoLoad: () => { console.log((<any>window).monaco); }
// };

const ip = environment.SERVER_IP;

@NgModule({
  declarations: [
    IdecontentComponent,
    IdepropertypanelComponent,
    IdecontrolpanelComponent,
    IdecontroltoolboxComponent,
    IdecontroloutlineComponent,
    IdeCodeEditorComponent,
    WorkspaceComponent,
    TestComponent,
    ModalContainerComponent
  ],
  imports: [
    FormsModule,
    HttpClientModule,
    LayoutModule,
    MainPanelModule,
    ModalModule.forRoot(),
    GridModule,
    HttpModule,
    IDELayoutModule,
    IDECommonModule,
    FileTreePanelPackageModule,
    // CacheModule.forRoot(),
    FarrisDialogModule,
    IDERootingModule,
    GSPMetadataServiceModule.forRoot(ip)
  ],
  providers: [
    ConfigService,
    HttpClient,
    PackageManager,
    WorkspaceManager
  ],
  bootstrap: [WorkspaceComponent],
  entryComponents: [IdecontentComponent, IdeCodeEditorComponent, IdecontentwrapperComponent, ModalContainerComponent]
})
export class IDEModule { }



