import { Component, OnInit, Input, ComponentFactoryResolver, ViewContainerRef, ViewChild, Type, OnChanges } from '@angular/core';
import { IdecontentComponent } from '../idecontent/idecontent.component';
import { IdeCodeEditorComponent } from '../ide-code-editor/ide-code-editor.component';

@Component({
  selector: 'app-idecontentwrapper',
  templateUrl: './idecontentwrapper.component.html',
  styleUrls: ['./idecontentwrapper.component.css']
})
export class IdecontentwrapperComponent implements OnChanges {

  @Input() contentUri: string;

  @Input() content: Type<any>;

  @ViewChild('idewrapper', { read: ViewContainerRef }) ideWrapper;

  constructor(private componentFactoryResolver: ComponentFactoryResolver,
    private viewContainerRef: ViewContainerRef) { }

  ngOnChanges() {
    let componentFactory: any;
    if (this.contentUri) {
      switch (this.contentUri) {
        case 'editor':
          componentFactory = this.componentFactoryResolver.resolveComponentFactory(IdeCodeEditorComponent);
          break;
        case 'designer':
          componentFactory = this.componentFactoryResolver.resolveComponentFactory(IdecontentComponent);
          break;
        default:
          throw new Error(`未识别的类型${this.contentUri}`);
      }
    }

    if (this.content) {
      componentFactory = this.componentFactoryResolver.resolveComponentFactory(this.content);
    }

    const ideWrapper = this.ideWrapper;
    ideWrapper.clear();
    if (componentFactory) {
      const componentRef = ideWrapper.createComponent(componentFactory);
    }
  }

}



