import {
  Compiler, Component, ComponentFactory, ComponentRef, Injector, Input, OnChanges, OnInit, ViewChild,
  ViewContainerRef
} from '@angular/core';
import { EventBus, GSP, Package, PackageModule } from '@farris/ide-devkit';
import {ConfigService} from '../../services/config.service';

declare var lazyRequire: any;

@Component({
  selector: 'app-plugin-container',
  templateUrl: './plugin-container.component.html',
  styleUrls: ['./plugin-container.component.css']
})
export class PluginContainerComponent implements OnInit, OnChanges {

  @ViewChild('pluginContainer', {read: ViewContainerRef}) container: ViewContainerRef;

  @Input() id: string;
  @Input() pluginType: string;
  @Input() componentRef: ComponentRef<any>;
  @Input() tabIndex: number;

  private gsp: GSP;
  private packageInstance: Package;
  private compFactory: ComponentFactory<any>;
  private loaded = false;
  private _componentRef: ComponentRef<any>;

  constructor(private configService: ConfigService,
               private eventBus: EventBus,
               private compiler: Compiler,
               private injector: Injector) {
    this.gsp = null;
  }

  ngOnInit() {
    console.log('plugin container init')
    if (this.tabIndex !== undefined && this.tabIndex !== null) {
      const eventBusProxy = this.gsp.eventBus.getProxy(PluginContainerComponent, () => this.constructor.name);
      setTimeout(() => {
        eventBusProxy.post('ActiveTabs', {area: 'main', index: this.tabIndex});
      });
    }
  }

  ngOnChanges() {
    this.container.clear();
    if (this.componentRef) {
      this.container.insert(this.componentRef.hostView);
    }
  }

}



