import { IFileTreeData } from './IFileTreeData';
import { TreeNodeData } from '../models/tree-node-data';

export class FileTreeDataAdapter implements IFileTreeData{
  // https://stackoverflow.com/questions/42396804/how-to-write-a-service-constructor-that-requires-parameters-in-angular-2
  constructor(private sourceServerTreeData: any){

  }

  /**
   * 对服务器端数据进行适配，获取构建文件树所需数据（服务器树结构不同，数据适配方式也不同）
   * @example
    {
      "node":{
        "name":"/",
        "type":"folder",
      }
      children: {
        "node":{
          "name":"/",
          "type":"folder",
        }
        children:{}
      }
    }
    => (适配)
    {
      "node":{
        "name":"/",
        "type":"folder",
        "children": {
          "node":{
            "name":"/",
            "type":"folder",
            children:{}
          }
        }
      }
    }
   */
  getAdaptedServerData(sourceServerTreeData: any = this.sourceServerTreeData): TreeNodeData{
    
    if(sourceServerTreeData == null){
      return null;
    }
    
    // ServeTreerData2LocalFileTreeData
    let treeNodeData = new TreeNodeData();
    
    treeNodeData.name = sourceServerTreeData 
      && sourceServerTreeData["node"] 
      && sourceServerTreeData["node"]["name"];
    treeNodeData.type = sourceServerTreeData 
      && sourceServerTreeData["node"] 
      && sourceServerTreeData["node"]["type"];
    treeNodeData.path = sourceServerTreeData 
      && sourceServerTreeData["node"] 
      && sourceServerTreeData["node"]["path"];
    
    if(sourceServerTreeData["children"] == null || sourceServerTreeData["children"].length <= 0){
      return treeNodeData; // 回归条件
    }
    
    sourceServerTreeData.children.forEach(element => {
      let localtreeNodeData = this.getAdaptedServerData(element);// 递归触发时机
      treeNodeData.children.push(localtreeNodeData);
    });

    return treeNodeData; // 最终返回值
  }
  /**
   * 对前端文件树数据进行适配，以返回给服务器
   */
  getAdaptedClientData(sourceClientTreeData:TreeNodeData): any{

  }

}


