import { Component, Input, ElementRef, Injector, OnInit } from '@angular/core';
import { ITreeNodeContent } from './ITreeNodeContent';
import { WorkspaceManager } from '../../../../workspace/workspace.manager';

@Component({
  selector: 'app-tree-node-content',
  templateUrl: './tree-node-content.component.html',
  styleUrls: ['./tree-node-content.component.css']
})
export class TreeNodeContentComponent implements ITreeNodeContent, OnInit {

  contentWidth = 0;

  @Input() displayData: any;

  constructor(private el: ElementRef, private injector: Injector) {
    this.injector.get(WorkspaceManager).leftSidebarResize.subscribe((panel) => {
      if (panel.isOpen) {
        this.initContentWidth();
      }
    });
  }

  ngOnInit() {
    this.initContentWidth();
  }

  private initContentWidth() {
    const offset_left = this.el.nativeElement.offsetLeft;
    // const realWidth = el.nativeElement.scrollWidth;
    if (this.el.nativeElement.offsetParent) {
      const containerWidth = this.el.nativeElement.offsetParent.clientWidth - 20;
      this.contentWidth = containerWidth - offset_left;
    }
  }


}



