
import { Component, OnInit, EventEmitter } from '@angular/core';
import { Subject } from 'rxjs/Subject';
import { pick } from 'lodash';
import { EventBusProxy } from '@farris/lazy-require';
import { FileTreeDataService } from '../../services/file-tree-data.service';
import { GitDataService, BranchData } from '../../services/git-data.service';
import { TreeNodeData } from '../../models/tree-node-data';

// import { Workspace } from '../../../../workspace/workspace';
import { FileTreePanelState } from './file-tree-panel.state';
import { FileTreePanelService, FILE_TREE_PANEL_EVENTS } from './file-tree-panel.service';
import { FileTreePanelOptions, ACTION } from './file-tree-panel.options';
import { Action } from '@progress/kendo-angular-dateinputs/dist/es2015/calendar/models/navigation-action.enum';

@Component({
  selector: 'app-file-tree-panel',
  templateUrl: './file-tree-panel.component.html',
  styleUrls: ['./file-tree-panel.component.css'],
})

export class FileTreePanelComponent implements OnInit {

  // 获取数据
  projectsData: TreeNodeData[];

  options: FileTreePanelOptions = new FileTreePanelOptions();

  currentGitProject = '';
  chosenBranch: BranchData;
  localBranches: BranchData[] = [];
  remoteBranches: BranchData[] = [];

  /**
   * 构造函数，注入依赖的服务
   * @param fileTreeDataService 注入文件树数据获取服务，以获取数据
   * @param fileTreePanelState 注入工作区服务，以与工作区交互
   * @param gitDataService 注入git数据服务，以与git服务交互
   */
  constructor(
    private fileTreePanelService: FileTreePanelService,
    public fileTreeDataService: FileTreeDataService,
    private fileTreePanelState: FileTreePanelState,
    private gitDataService: GitDataService
  ) {
    const self = this;
    // 获取数据
    self.getInitFileTreeData(false);

    // // 初始化自定义事件，并绑定事件处理器
    // fileTreePanelService.eventNames.forEach((name) => {
    //   // 自定义事件
    //   self[name] = new EventEmitter();
    //   const eventPair = pick(self, name);
    //   fileTreePanelService.addEvent(eventPair);

    //   // // 绑定事件处理器
    //   // switch(name){
    //   //   case FILE_TREE_PANEL_EVENTS.onFileTreeAccess:
    //   //     self[name].subscribe((data) => this.processRequest(data));//
    //   //     break;
    //   //   default:
    //   //     break;
    //   //   }
    // })
  }

  processRequest(params): any {
    if (params == null) {
      return;
    }

    let responseData = null;

    const action = params[this.options.actionField];
    switch (action) {
      case ACTION.createFileTreeNode:
        responseData = this.createFileTreeNode(params);
        break;
      case ACTION.retrieveFileTreeNode:
        responseData = this.retrieveFileTreeNode(params);
        break;
      case ACTION.updateFileTreeNode:
        responseData = this.updateFileTreeNode(params);
        break;
      case ACTION.deleteFileTreeNode:
        responseData = this.deleteFileTreeNode(params);
        break;
      case ACTION.refreshFileTree:
        responseData = this.refreshFileTree(params);
        break;
      case ACTION.getFocusedFileTreeNode:
        responseData = this.getFocusedFileTreeNode(params);
        break;
      default:
        break;
    }
    return responseData;
  }

  private createFileTreeNode(request): any {
    const nodeInfo = request.node;
    const nodeID: string = nodeInfo.nodeID;
    const nodeName: string = nodeInfo.nodeName;
    const nodeType: string = nodeInfo.nodeType;
    const relativePath: string = nodeInfo.nodePath;

    this.fileTreePanelService.treeModel.addFileTreeNode(nodeType, nodeID, nodeName, relativePath);
    return true;
  }

  private deleteFileTreeNode(request) {// 默认移除 focused Node
    this.fileTreePanelService.treeModel.removeFileTreeFocusedNode();
  }

  // todo:补充节点更新方法
  private updateFileTreeNode(request) {

  }

  // todo:补充节点查找方法
  private retrieveFileTreeNode(request) {

  }
  // 刷新文件树
  // todo: 支持局部加载
  private refreshFileTree(request) {
    this.getInitFileTreeData();
  }

  // 获取文件树节点
  private getFocusedFileTreeNode(request): any {
    const focusedNodeInfo = {
      nodeID: '',
      nodeName: '',
      nodeType: '',
      nodePath: ''
    };

    const focusedNode = this.fileTreePanelService.treeModel.FocusedNode;
    if (focusedNode == null) {
      return focusedNodeInfo;
    }
    // focusedNodeInfo.nodeID = focusedNode.idField
    focusedNodeInfo.nodeName = focusedNode.displayField;
    focusedNodeInfo.nodeType = focusedNode.fileTypeField;
    focusedNodeInfo.nodePath = focusedNode.pathField;

    return focusedNodeInfo;
  }

  /**
   * 获取根目录树
   * @param isMockedData 是否选用mock数据
   */
  getInitFileTreeData(isMockedData: boolean = false) {

    if (isMockedData) {// 使用mock数据（离线状态下）
      this.projectsData = this.fileTreeDataService.getChildrenDataWithMock();
      return;
    }

    this.fileTreeDataService.getChildrenData().subscribe(
      (data) => {
        this.projectsData = data;
      }
    );
  }

  // 自定义事件处理器
  toggleEventHandler = ($event) => console.log($event);

  activateEventHandler($event) {
    // 在TreeNode Actived时，传递treeNode info
    // [EventBus使用]3.通过EventBusProxy实例，向指定事件发送数据
    // this.ebproxy.post('FileTreeNodeActived', $event.node);
    this.fileTreePanelState.fileTreeNodeActived.emit($event.node);
  }

  deactivateEventHandler = ($event) => console.log($event);

  activeChangedEventHandler = ($event) => console.log($event);

  focusEventHandler($event) {
    console.log($event);
    this.fileTreePanelState.fileTreeNodeFocused.emit($event.node.pathField);

    // 选中节点时，更新git信息
    this.updateCurrentGitInfo();
  }

  blurEventHandler = ($event) => console.log($event);

  // 双击不同的文件，打开不同的设计器。如双击.BE文件，打开BE设计器
  doubleClickEventHandler($event) {
    console.log('DoubleClick:Open the File\'s Editor');
    // [EventBus2使用]2.通过EventBusProxy实例，向指定事件发送数据
    // this.ebproxy2 && this.ebproxy2.post('FileTreeNodeDoubleClick', $event.node);
    this.fileTreePanelState.fileTreeNodeDoubleClick.emit($event.node);
  }

  contextMenuEventHandler = ($event) => console.log('ContextMenu:Show ContextMenu');

  ngOnInit() {
    // [EventBus使用]1.创建EventBusProxy实例
    // this.ebproxy = this.workspace.eventBus.getProxy(this, () => 'FileTreePanelPackage');
    // [EventBus使用]2.使用EventBusProxy，注册event
    // this.ebproxy.register(['FileTreeNodeActived']);

    // [EventBus2使用]1.创建EventBusProxy实例
    // this.ebproxy2 = this.workspace.eventBus2.getProxy(FileTreePanelPackage, () => 'FileTreePanelPackage');

  }

  // 刷新功能
  refresh() {
    // this.fileTreePanelService.treeModel.refresh();
    this.refreshFileTree(null);
  }

  // todo: 全部折叠
  collapseAll() {
    this.fileTreePanelService.treeModel.collapseAll();
  }



  // 获取git branch数据
  fetchGitBranchData() {
    const self = this;
    if (self.currentGitProject === '') {
      // 清空已有历史记录
      self.remoteBranches = [];
      self.localBranches = [];
      console.log('Please choose a project with git repository');
      return;
    }
    self.gitDataService.getGitBranchData(self.currentGitProject).subscribe((data) => {
      const branches = data;
      self.remoteBranches = [];
      self.localBranches = [];
      branches.forEach((branch) => {
        if (branch.remote) {
          self.remoteBranches.push(branch);
        } else {
          self.localBranches.push(branch);
          if (branch.active) {
            self.chosenBranch = branch;

            // 发射当前 Git Branch displayName
            console.log('Emit Current Git Branch');
            self.fileTreePanelState.currentGitBranch.emit(this.chosenBranch.displayName);
          }
        }
      });
      console.log(' Already got branch list from server');
    });

  }

  gitCheckoutLocalBranch(branchName: string) {
    this._gitCheckout(branchName, false);
  }

  gitCheckoutRemoteBranch(branchName: string) {
    const shortBranchName: string = branchName.substring(branchName.indexOf('/') + 1, branchName.length);
    if (shortBranchName.length < 0) {
      return;
    }

    // 检查远程分支对应的本地分支是否已存在
    let existLocalBranch = false;
    this.localBranches.forEach((localBranch) => {
      if (localBranch.displayName === shortBranchName) {
        existLocalBranch = true;
      }
    });
    if (existLocalBranch) {
      return;
    }

    this._gitCheckout(shortBranchName, true);
  }

  _gitCheckout(branchName: string, createNewBranch: boolean = false) {
    const self = this;
    // 分支切换
    self.gitDataService.postGitCheckout(this.currentGitProject, branchName, createNewBranch).subscribe(
      result => {
        self.fetchGitBranchData();
        console.log('checkout branch:' + branchName);
      },
      error => {
        console.error('checkout branch:' + branchName + 'failed');
      }
    );
  }


  // 更新当前gitProject，并获取分支信息
  updateCurrentGitInfo() {
    const self = this;
    self.currentGitProject = ''; // 每切换一次分支，都重新初始化相关数据
    self.remoteBranches = [];
    self.localBranches = [];

    self.getGitProjectPath().subscribe(
      project => {
        self.currentGitProject = project;

        // 发射当前Git仓库信息
        console.log('Emit Current Git Reposity');
        self.fileTreePanelState.currentGitReposity.emit(this.currentGitProject);

        // 获取分支信息
        self.fetchGitBranchData();
      },
      error => {
        console.log('Error in get Current Git Reposity:' + error);
      }
    );

  }

  // 获取当前选中节点的最近git仓库的path
  getGitProjectPath(): Subject<string> {
    const self = this;
    // 调用服务获取最近git仓库的path
    const currentRelativePath = self.fileTreePanelService.treeModel.focusedNode.pathField;
    const gitProjectPath = new Subject<string>();
    self.gitDataService.getGitAddress(currentRelativePath).subscribe(
      projectPath => {
        const regxProjectPath = projectPath.replace(/\\/g, '/');
        if (regxProjectPath === '') {
          console.log('Current Project does not have a git depository');
        }

        gitProjectPath.next(regxProjectPath);
      }
    );
    return gitProjectPath;
  }
}



